export default {
	data() {
		return {
			localTime : ''
		};
	},
	onLoad() {
		this.getDate()
	},
	methods:{
		getDate(){
			var date = new Date();
			var formatDate = date.getFullYear() + '年' + (date.getMonth() + 1) + '月' + date.getDate() + '日 ' + date.getHours() + ':' + (date.getMinutes()>10?date.getMinutes():'0'+date.getMinutes());
			this.localTime = formatDate
		}
	}
}
